import React from "react";
import { Item } from "./item";
import { ItemShowcase } from "./ItemShowcase";
const MetaTags = require('react-meta-tags');

export class ItemShare extends React.Component<{encodedItem?: string, path: string}> {

    render() {
        const item: Item = JSON.parse(decodeURIComponent(this.props.encodedItem ?? ''));
        return (<div>
            <MetaTags>
                <title>Page 1</title>
                <meta name="description" content="Some description." />
                <meta property="og:title" content="MyApp" />
                <meta property="og:image" content={process.env.PUBLIC_URL + '/background.jpg'} />
            </MetaTags>
            <ItemShowcase item={item}></ItemShowcase>
        </div>);
    }
}